document.addEventListener('turbolinks:load', function() {
  $('ul.dropdown-menu [data-toggle=dropdown]').on('click', function(event) {
    event.preventDefault();
    event.stopPropagation();
    $(this)
      .parent()
      .siblings()
      .removeClass('open');
    $(this)
      .parent()
      .toggleClass('open');
  });
});



